}
   /* ================== SMALL DEVICES   ======================== */
   
   
   @media screen and (max-width:599px) and (min-width:200px){
   

   .permalinks{
    flex-direction: column;
    gap: 1.5rem;
   }
   
   .footer__socials{
    margin-bottom: 2.6rem;
   }
   
   
   }